import React from 'react';

import { Rectangle as RectangleClass } from './Figures/Rectangle';
import { Line as LineClass } from './Figures/Line';

const Rectangle = new RectangleClass();
const Line = new LineClass();

export class Geometry {
	getRectangle(config) {
		const dots = [];
		const params = this.getCommonParams(config);

		for (let i = 0; i < params.dotsCount; i++) {
			const dotsStyle = Rectangle.getRectangleDot(i, params);

            dots.push({ style: this.getStyle(dotsStyle) });
        }

        return dots;
	}

	getDirectLine(config) {
		const dots = [];
		const params = this.getCommonParams(config);

		for (let i = 0; i < params.dotsCount; i++) {
			const dotsStyle = Line.getLineDot(i, params)
			
			dots.push({ style: this.getStyle(dotsStyle) });
        }

        return dots;
	}
	
	getCommonParams(config) {
		const { dotsCount, dotsSize, coords } = config;

		return {
			dotsCount,
			dotsSize,
			width: coords.width,
			height: coords.height
		};
	}

	getStyle({
		x = 0,
		y = 0,
		bgColor = ''
	}) {
		const style = {};

		if (bgColor) {
			style.backgroundColor = bgColor;
		}

		style.transform = `translate3d(${x}px, ${y}px, 0)`;

		return style;
	}
};
