import React from 'react';
import Figures from '.';

export class Line {
	getLineDot(i, params) {
        const { dotsSize, dotsCount, width, height } = params;

        const x = Figures.getDotOffsetByDistance(width, dotsSize, dotsCount, i);
        const y = height / 2 - dotsSize / 2;

		return { x, y };
    }
};
